import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { EventsPage } from '../events/events';
import { Connect } from '../../providers/connect';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

 public feed : any[];
 public banner : any = './assets/img/blankbanner.jpg';
 public calRoot : any;
  constructor(public navCtrl: NavController, private connect : Connect) {
    this.feed = [];
    this.calRoot = EventsPage;
  }

  ionViewDidEnter()  {
     let that=this;
        let url = 'zs_socialfeed.php';
        this.connect.getList(url).subscribe(
         data => {
            that.feed = data.feed;
            that.banner = data.banner;             
         },
         err => {
         }
        ); 
  }

}




